    __html: "<p>Apex’s Professional Scrum Product Owner training is your pathway to Scrum Certification!</p><p>Apex’s Professional Scrum Product Owner training prepares you for Scrum.org’s Professional Scrum Product Owner (PSPO) certification examination. You will learn from the best: our training is given by industry experts who have decades of experience applying Agile Scrum at leading companies like IBM, edX, PricewaterhouseCoopers, and Boston Consulting Group. In addition, Apex’s founder, Raj Heda, is currently on the faculty at Boston University and Brandeis University.</p><h4>Training Overview</h4><p>This two-day course is intended for those with a background in the software industry, whether or not you have previous experience as a product owner.  From the product owner’s perspective we examine the key concepts behind iterative software development in an Agile Scrum environment.  Our instructors provide real examples from industry that demonstrate the product owner’s roles and responsibilities.  From creating user stories to managing product backlogs, to  demonstrating value and incremental and iterative delivery, the product owner’s role is clearly explained  Our instructors also cover the daily responsibilities of Agile team members and their use of Scrum, Kanban, and Extreme Programming.</p><p>Unlike any other training program, we offer the following bonuses:</p><ul><li>The highest passing rates: The PSPO certification exam is rigorous, but you will be prepared. Nearly 100% of our graduates pass the exam on their very first attempt. In the unlikely event that you would need to retake the exam, a second trial is free and additional examinations are discounted.</li><li>Free follow-up access to your course materials for one year.</li><li>25% discount on future course fees. Save on courses as you sail through the certification process.</li></ul><p>Anytime availability: Your course are available over the Internet 7 days a week and 24 hours a day. Study at any day and time you want. Once you are ready, simply sign up to take the certification exam!</p><p><b>Prerequisites:</b> Some experience with Agile software development principles and practices will be helpful, whether from actual industry experience or reading.</p>",
